___CSS_LOADER_EXPORT___.push([module.id, `.task-wrapper {
  background-color: white;
  border-radius: 5px;
  box-shadow: 0 0 5px 2px rgba(0, 0, 0, 0.2);
  margin-bottom: 10px;
  padding: 10px;
  display: flex;
  align-items: center;
}

.task-section-left {
  display: flex;
}

.task-section-right {
  display: flex;
}

.task-info-wrapper {
  display: flex;
  flex-direction: column;
  flex: 1;
}

.task-title {
  font-size: 1.2em;
  font-weight: 600;
}

.task-description {
  font-size: 0.9em;
  color: var(--text-color-light-grey);
  display: none;
}

.show-description {
  display: flex !important;
  transition: 0.9s;
  opacity: 1;
}

.task-date {
  font-size: 1em;
  margin-right: 15px;
  font-weight: 600;
}

.task-section-right {
  display: flex;
  align-items: center;
}

.task-priority {
  height: 30px;
  margin: 0 15px;
}

.task-completed-btn {
  height: 24px;
  margin-right: 15px;
  margin-top: 1px;
}

.task-delete-btn,
.task-completed-btn:hover {
  cursor: pointer;
}

.task-completed {
  text-decoration: line-through;
  opacity: 0.7;
}
